<template>
  <!-- Main -->
  <div class="md:gap-4 p-6 md:grid dark:bg-gray-800 overflow-x-hidden">
    <div data-aos="fade-in" data-aos-duration="1000" class="grid grid-cols-2 gap-4 justify-between lg:px-14 mb-10 md:mb-8 dark:text-white">
      <h1 class="justify-start md:text-left text-4xl font-semibold">Projects</h1>
      <p class="text-xl col-span-2">In my spare time I like to tinker on side projects. These are some of the results.</p>
    </div>
    <template v-for="(project, index) in projects" :key="index">
      <div :data-aos="index % 2 ? 'fade-left' : 'fade-right'" data-aos-duration="1200" class="md:grid items-center grid-cols-3 gap-8 lg:px-24 dark:text-white mb-8">
        <div class="col-span-2 object-center w-full md:h-100 overflow-hidden shadow-lg transform hover:scale-105 transition-transform duration-300 ease-in-out mb-3 md:mb-0">
          <img v-lazy="getImgUrl(project.image[0])" :alt="`${project.title} image ${index}`" class="m-auto object-contain w-full h-full border-gray-900 border-2 rounded-md">
        </div>
        <div>
          <h2 class="text-3xl font-semibold">{{project.title}}</h2>
          <small class="text-gray-400 mb-5 text-lg">Date Finished: {{project.date}}</small>
          <p class="leading-relaxed mb-3 text-lg">{{project.description}}</p>
          <div class="mb-5 flex flex-wrap">
            <template v-for="(tag, index) in project.tag" :key="index">
              <span @click="$router.push({name: 'tag', query: { q: tag }})" class="font-normal m-1 bg-gray-200 hover:bg-gray-300 rounded-full px-2 text-sm leading-loose cursor-pointer transition-colors duration-300 dark:bg-gray-600 dark:hover:bg-gray-500">
                {{tag}}
              </span>
            </template>
          </div>
          <div class="flex items-center">
            <router-link :to="{name: 'projectDetail', params: {slug: project.slug}}" class="py-2 px-3 rounded-full bg-indigo-400 border-2 border-indigo-400 font-semibold sm:text-lg text-center md:text-sm lg:text-lg text-white transition-all duration-300 ease-out hover:bg-transparent hover:text-indigo-400 focus:bg-transparent focus:text-indigo-400 dark:bg-indigo-700 dark:border-indigo-700 dark:hover:bg-transparent dark:hover:text-indigo-600 dark:focus:bg-transparent dark:focus:text-indigo-600">View Details</router-link>
            <a v-if="project.website" :href="project.website" target="_blank" class="px-3 py-2 flex transition duration-200 mx-4 sm:text-lg text-center md:text-sm lg:text-lg font-semibold hover:text-indigo-700 focus:text-indigo-800 dark:hover:text-indigo-400">
              Website
            </a>
          </div>
        </div>
      </div>
    </template>
    <div class="flex justify-center items-center py-4">
      <a href="https://github.com/firmanmimang" class="flex items-center py-2 px-6 rounded-full bg-gray-700 border-2 border-gray-700 font-semibold text-lg text-white transition-all duration-300 ease-out transform hover:scale-110 hover:shadow-xl">
        View More On My Github&nbsp;<img src="../assets/GitHub-Mark-Light-64px.png" alt="github icon" class="h-8 opacity-75">
      </a>
    </div>
  </div>
</template>

<script>
import {getProjectImgUrl} from '@/utils'
export default {
  computed: {
    projects(){
      return this.$store.getters.allProjects
    }
  },
  methods: {
    getImgUrl(pic) {
      return getProjectImgUrl(pic)
    }
  }
};
</script>

